<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import apiServices from "../components/Service/apiservice";
import constants from "../components/Service/constants.js";

export default {
  name: "Contact",
  data() {
    return {
      users:{},
      user:{},
      username: "",
      phone: "",
      email: "",
      message: "",
      
    };
  },
    mounted() {
    this.users = JSON.parse(localStorage.getItem("Login_data"));
  },
  components: {
    Header,
    Footer,
  },
   methods: {
      async contactus() {
        this.username = "";
        this.phone = "";
        this.email = "";
        this.message = "";

      if (!this.user.name) {
        this.username = constants.username;
      }
      if (!this.user.phone) {
        this.phone = constants.phone;
      }
      if (!this.user.email) {
        this.email = constants.email;
      }
      if (!this.user.message) {
        this.message = constants.message;
      }
      if (this.username == "" && this.phone == "" && this.email == "" && this.message == "") {
         const formData = new FormData()

        formData.append("lang",'0');
        formData.append("name",this.user.name)
        formData.append("mobile_no",this.user.phone)
        formData.append("email",this.user.email)
        formData.append("message",this.user.message)
        formData.append("is_login","0")

      let res = await apiServices.makeAPICall(
        apiServices.postMethod,
        apiServices.URL + apiServices.contact_usAPI,
        formData,
        () => {}
      );
      if (res.data.flag == 1) {
        this.ToastAlert("success", res.data.msg);
      } else {
        this.ToastAlert("error", res.data.msg);
      }
      }
    },
    
    ToastAlert(id, msg) {
      const Toast = this.$swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: false,
        // didOpen: (toast) => {
        //   toast.addEventListener('mouseenter', this.$swal.stopTimer)
        //   toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        // }
      });

      Toast.fire({
        icon: id,
        title: msg,
      });
    },
   }
};
</script>
